import { IgcScatterPolylineSeriesModule } from 'igniteui-charts/igc-scatter-polyline-series-module';

import { IgcGeographicPolylineSeriesComponent } from './igc-geographic-polyline-series-component';
import { GeographicPolylineSeries } from './GeographicPolylineSeries';
import { TypeRegistrar } from 'igniteui-core/type';

export class IgcGeographicPolylineSeriesModule {
    public static register(): void {
IgcScatterPolylineSeriesModule.register();
        IgcGeographicPolylineSeriesComponent.register();
        TypeRegistrar.registerCons("IgcGeographicPolylineSeriesComponent", IgcGeographicPolylineSeriesComponent);
        TypeRegistrar.register("GeographicPolylineSeries", (<any>GeographicPolylineSeries).$type);
    }
}
