import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { delegateCombine, delegateRemove } from 'igniteui-core/type';
import { IgcGetTileImageUriArgs } from "igniteui-core/igc-get-tile-image-uri-args";
import { IgcGeographicMapImagery } from "./igc-geographic-map-imagery";
import { CustomMapImagery as CustomMapImagery_internal } from "./CustomMapImagery";

/**
 * Represents a CustomMapImagery MapImagery
*/
export class IgcCustomMapImagery extends IgcGeographicMapImagery
{


	/**
	                             * @hidden 
	                             */
	                            public get i() : CustomMapImagery_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	
	    private _getTileImageUri: (s: IgcCustomMapImagery, e: IgcGetTileImageUriArgs) => void = null;
	    private _getTileImageUri_wrapped: any = null;
	/**
	 * Raised when URI for tiles should be provided
	*/
	
	public get getTileImageUri(): (s: IgcCustomMapImagery, e: IgcGetTileImageUriArgs) => void {
	return this._getTileImageUri;
	}
	    public set getTileImageUri(ev: (s: IgcCustomMapImagery, e: IgcGetTileImageUriArgs) => void) {
	        if (this._getTileImageUri_wrapped !== null) {
	            this.i.getTileImageUri = delegateRemove(this.i.getTileImageUri, this._getTileImageUri_wrapped);
	            this._getTileImageUri_wrapped = null;
	            this._getTileImageUri = null;
	        }
	
	        this._getTileImageUri = ev;
	        this._getTileImageUri_wrapped = (o, e) => {
	            
	                    let outerArgs = new IgcGetTileImageUriArgs();
	(<any>outerArgs)._provideImplementation(e);
	                    if ((this as any).beforeGetTileImageUri) {
	                        (this as any).beforeGetTileImageUri(this, outerArgs);
	                    }
	                    if (this._getTileImageUri) {
	    this._getTileImageUri(this, outerArgs);
	                    }
	
	        };
	        this.i.getTileImageUri = delegateCombine(this.i.getTileImageUri, this._getTileImageUri_wrapped);;
	    }
}
