import { IgcBingMapsMapImageryModule } from './igc-bing-maps-map-imagery-module';
import { IgcOpenStreetMapImageryModule } from './igc-open-street-map-imagery-module';
import { IgcCustomMapImageryModule } from './igc-custom-map-imagery-module';
import { IgcArcGISOnlineMapImageryModule } from './igc-arc-gis-online-map-imagery-module';
import { IgcGeographicMapComponent } from './igc-geographic-map-component';
import { TypeRegistrar } from 'igniteui-core/type';


export class IgcGeographicMapCoreModule {
    public static register() {
                IgcBingMapsMapImageryModule.register();
        IgcOpenStreetMapImageryModule.register();
        IgcCustomMapImageryModule.register();
        IgcArcGISOnlineMapImageryModule.register();
        IgcGeographicMapComponent.register();
        TypeRegistrar.registerCons('IgcGeographicMapComponent', IgcGeographicMapComponent);
    }
}