import { IgcScatterSeriesModule } from 'igniteui-charts/igc-scatter-series-module';

import { IgcGeographicSymbolSeriesComponent } from './igc-geographic-symbol-series-component';
import { GeographicSymbolSeries } from './GeographicSymbolSeries';
import { TypeRegistrar } from 'igniteui-core/type';

export class IgcGeographicSymbolSeriesModule {
    public static register(): void {
IgcScatterSeriesModule.register();
        IgcGeographicSymbolSeriesComponent.register();
        TypeRegistrar.registerCons("IgcGeographicSymbolSeriesComponent", IgcGeographicSymbolSeriesComponent);
        TypeRegistrar.register("GeographicSymbolSeries", (<any>GeographicSymbolSeries).$type);
    }
}
