import { IgcOpenStreetMapImagery } from './igc-open-street-map-imagery';
import { OpenStreetMapImagery } from './OpenStreetMapImagery';
import { TypeRegistrar } from 'igniteui-core/type';

export class IgcOpenStreetMapImageryModule {
    public static register(): void {
        
        TypeRegistrar.registerCons("IgcOpenStreetMapImagery", IgcOpenStreetMapImagery);
        TypeRegistrar.register("OpenStreetMapImagery", (<any>OpenStreetMapImagery).$type);
    }
}
