import { IgcScatterContourSeriesModule } from 'igniteui-charts/igc-scatter-contour-series-module';

import { IgcGeographicContourLineSeriesComponent } from './igc-geographic-contour-line-series-component';
import { GeographicContourLineSeries } from './GeographicContourLineSeries';
import { TypeRegistrar } from 'igniteui-core/type';

export class IgcGeographicContourLineSeriesModule {
    public static register(): void {
IgcScatterContourSeriesModule.register();
        IgcGeographicContourLineSeriesComponent.register();
        TypeRegistrar.registerCons("IgcGeographicContourLineSeriesComponent", IgcGeographicContourLineSeriesComponent);
        TypeRegistrar.register("GeographicContourLineSeries", (<any>GeographicContourLineSeries).$type);
    }
}
