import { IgcCustomMapImagery } from './igc-custom-map-imagery';
import { CustomMapImagery } from './CustomMapImagery';
import { TypeRegistrar } from 'igniteui-core/type';

export class IgcCustomMapImageryModule {
    public static register(): void {
        
        TypeRegistrar.registerCons("IgcCustomMapImagery", IgcCustomMapImagery);
        TypeRegistrar.register("CustomMapImagery", (<any>CustomMapImagery).$type);
    }
}
