import { IgcScatterPolygonSeriesModule } from 'igniteui-charts/igc-scatter-polygon-series-module';

import { IgcGeographicShapeSeriesComponent } from './igc-geographic-shape-series-component';
import { GeographicShapeSeries } from './GeographicShapeSeries';
import { TypeRegistrar } from 'igniteui-core/type';

export class IgcGeographicShapeSeriesModule {
    public static register(): void {
IgcScatterPolygonSeriesModule.register();
        IgcGeographicShapeSeriesComponent.register();
        TypeRegistrar.registerCons("IgcGeographicShapeSeriesComponent", IgcGeographicShapeSeriesComponent);
        TypeRegistrar.register("GeographicShapeSeries", (<any>GeographicShapeSeries).$type);
    }
}
