import { IgRect } from "igniteui-core/IgRect"
import { IgSize } from "igniteui-core/IgSize"
import { IgPoint } from "igniteui-core/IgPoint"
import { IgDataTemplate } from "igniteui-core/IgDataTemplate"
import { IgcGeographicMapSeriesHostComponent } from "./igc-geographic-map-series-host-component";
import { IgcSeriesComponent } from "igniteui-charts/igc-series-component";
import { GeographicXYTriangulatingSeries } from "./GeographicXYTriangulatingSeries";
import { getAllPropertyNames, fromSpinal, toSpinal, initializePropertiesFromCss, NamePatcher } from "igniteui-core/componentUtil";
import { IgcTriangulationDataSource } from "igniteui-core/igc-triangulation-data-source";
import { IEnumerable } from "igniteui-core/type";
import { RegisterElementHelper } from 'igniteui-core/module-manager';

/**
 * Base class for geographic series which triangulate XY data prior to rendering.
*/
export abstract class IgcGeographicXYTriangulatingSeriesComponent extends IgcGeographicMapSeriesHostComponent {


    public get i(): GeographicXYTriangulatingSeries {
        return this._implementation;
    }
    constructor() {
        super();
    }

    private static _observedAttributesIgcGeographicXYTriangulatingSeriesComponent: string[] = null;
    static get observedAttributes() {
        if (IgcGeographicXYTriangulatingSeriesComponent._observedAttributesIgcGeographicXYTriangulatingSeriesComponent == null) {
            let names = getAllPropertyNames(IgcGeographicXYTriangulatingSeriesComponent);
            for (let i = 0; i < names.length; i++) {
                names[i] = toSpinal(names[i]);
            }
            IgcGeographicXYTriangulatingSeriesComponent._observedAttributesIgcGeographicXYTriangulatingSeriesComponent = names;
        }
        return IgcGeographicXYTriangulatingSeriesComponent._observedAttributesIgcGeographicXYTriangulatingSeriesComponent;
    }

    private _triangulationDataSource: string;
	/**
	 * Gets or sets the string path specifying the location of the Itf file.
	*/
    set triangulationDataSource(value: string) {
        this._triangulationDataSource = value;
        this.updateTriangulationDataSource();
    }
    get triangulationDataSource(): string {
        return this._triangulationDataSource;
    }

    private _cachedTriangulationDataSource: IgcTriangulationDataSource;
    private updateTriangulationDataSource() {
        if (this._cachedTriangulationDataSource == null) {
            this._cachedTriangulationDataSource = new IgcTriangulationDataSource();
            this.onTriangulationDataSourceImportCompleted = this.onTriangulationDataSourceImportCompleted.bind(this);
            this._cachedTriangulationDataSource.importCompleted = this.onTriangulationDataSourceImportCompleted;
        }

       	this._cachedTriangulationDataSource.source = this._triangulationDataSource;
    }

    private onTriangulationDataSourceImportCompleted(s: any, e: any) {
        this.dataSource = this._cachedTriangulationDataSource.getPointData();
        this.trianglesSource = <any[]><any>this._cachedTriangulationDataSource.getTriangleData();
    }

		/**
	 * The name of the property from which to extract the Longitude for each item in the ItemsSource.
	*/
	get longitudeMemberPath() : string {
		return (this.i.ug as string);
	}
	set longitudeMemberPath(v: string) {
		this.i.ug = v;
	}
	/**
	 * The name of the property from which to extract the Latitude for each item in the ItemsSource.
	*/
	get latitudeMemberPath() : string {
		return (this.i.ue as string);
	}
	set latitudeMemberPath(v: string) {
		this.i.ue = v;
	}
	/**
	 * The source of triangulation data.
	 * This property is optional.  If it is left as null, the triangulation will be created based on the items in the ItemsSource.  Triangulation is a demanding operation, so the runtime performance will be better when specifying a TriangulationSource, especially when a large number of data items are present.
	*/
	get trianglesSource() : any[] {
		return (this.i.ud as any) as any[];
	}
	set trianglesSource(v: any[]) {
	                                        this.i.ud = (v as any) as IEnumerable;
	}
	/**
	 * The name of the property of the TrianglesSource items which, for each triangle, contains the index of the first vertex point in the ItemsSource.
	*/
	get triangleVertexMemberPath1() : string {
		return (this.i.uj as string);
	}
	set triangleVertexMemberPath1(v: string) {
		this.i.uj = v;
	}
	/**
	 * The name of the property of the TrianglesSource items which, for each triangle, contains the index of the second vertex point in the ItemsSource.
	*/
	get triangleVertexMemberPath2() : string {
		return (this.i.ul as string);
	}
	set triangleVertexMemberPath2(v: string) {
		this.i.ul = v;
	}
	/**
	 * The name of the property of the TrianglesSource items which, for each triangle, contains the index of the third vertex point in the ItemsSource.
	*/
	get triangleVertexMemberPath3() : string {
		return (this.i.un as string);
	}
	set triangleVertexMemberPath3(v: string) {
		this.i.un = v;
	}

    

    

	

    

    //@@ZoneRunner
    
}
