import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { delegateCombine, delegateRemove } from 'igniteui-core/type';
import { IgcGeographicMapImagery } from "./igc-geographic-map-imagery";
import { IgcShapeSeriesBaseComponent } from "igniteui-charts/igc-shape-series-base-component";
import { IgcSeriesComponent } from "igniteui-charts/igc-series-component";
import { TileSeries } from "./TileSeries";
import { getAllPropertyNames, fromSpinal, toSpinal, ensureBool } from "igniteui-core/componentUtil";
import { RegisterElementHelper } from "igniteui-core/module-manager";

/**
 * Represents a tile series class that can render imagery tiles
*/
export class IgcTileSeriesComponent extends IgcShapeSeriesBaseComponent
{

protected createImplementation() : TileSeries
{
	return new TileSeries();
}
	/**
	                             * @hidden 
	                             */
	                            public get i() : TileSeries {
		return this._implementation;
	}
	constructor() {
		super()
	                                     
	}
	
	    connectedCallback() {
	        if (super["connectedCallback"]) {
	            super["connectedCallback"]();
	        }
	        if ((<any>this.i).connectedCallback) {
	            (<any>this.i).connectedCallback();
	        }
	        
	        if (!this._attached) {
	            this._attached = true;
	            this._flushQueuedAttributes();
	        }
	    }
	    disconnectedCallback() {
	        if (super["disconnectedCallback"]) {
	            super["disconnectedCallback"]();
	        }
	        if ((<any>this.i).disconnectedCallback) {
	            (<any>this.i).disconnectedCallback();
	        }
	        if (this._attached) {
	            this._attached = false;
	        }
	    }
	
	                private static _observedAttributesIgcTileSeriesComponent: string[] = null;
	                static get observedAttributes() {
	                    if (IgcTileSeriesComponent._observedAttributesIgcTileSeriesComponent == null) {
	                        let names = getAllPropertyNames(IgcTileSeriesComponent);
	    for (let i = 0; i < names.length; i++) {
	                            names[i] = toSpinal(names[i]);
	    }
	    IgcTileSeriesComponent._observedAttributesIgcTileSeriesComponent = names;
	                    }
	                    return IgcTileSeriesComponent._observedAttributesIgcTileSeriesComponent;               
	                }                 
	            
	
	                public static htmlTagName: string = "igc-tile-series";
	                protected static _isElementRegistered = false;
	
	                public static register() {
	                    if (!IgcTileSeriesComponent._isElementRegistered) {
	                        IgcTileSeriesComponent._isElementRegistered = true;
	                        RegisterElementHelper.registerElement(
	                            IgcTileSeriesComponent.htmlTagName,
	                            IgcTileSeriesComponent);
	                    }
	            }
	/**
	 * Gets whether the current series shows a tile imagery.
	*/
	get isTile() : boolean {
		return (this.i.fh as boolean);
	}
	/**
	 * The MapImagery which is used to provide the tiles for display.
	*/
	get tileImagery() : IgcGeographicMapImagery {
	                                        const r = this.i.wx;
	                                        if (r == null) {
	                                            return null;
	                                        }
	                                        
		return (r as any).externalObject;
	}
	set tileImagery(v: IgcGeographicMapImagery) {
		v == null ? this.i.wx = null : this.i.wx = v.i;
	}
	
	    public findByName(name: string): any {
	        
	    var baseResult = super.findByName(name);
	    if (baseResult)
	    {
	        return baseResult;
	    }
	
	if (this.tileImagery && (this.tileImagery as any).name && (this.tileImagery as any).name == name) { return this.tileImagery; }
	
	        return null;
	    }
	/**
	 * Defers rendering of the series
	
	*/
	public deferredRefresh() {
		this.i.deferredRefresh();
	}
}
