import { IgcTileSeriesComponent } from './igc-tile-series-component';
import { TileSeries } from './TileSeries';
import { TypeRegistrar } from 'igniteui-core/type';

export class IgcTileSeriesModule {
    public static register(): void {
        IgcTileSeriesComponent.register();
        TypeRegistrar.registerCons("IgcTileSeriesComponent", IgcTileSeriesComponent);
        TypeRegistrar.register("TileSeries", (<any>TileSeries).$type);
    }
}
