import { IgcBubbleSeriesModule } from 'igniteui-charts/igc-bubble-series-module';

import { IgcGeographicProportionalSymbolSeriesComponent } from './igc-geographic-proportional-symbol-series-component';
import { GeographicProportionalSymbolSeries } from './GeographicProportionalSymbolSeries';
import { TypeRegistrar } from 'igniteui-core/type';

export class IgcGeographicProportionalSymbolSeriesModule {
    public static register(): void {
IgcBubbleSeriesModule.register();
        IgcGeographicProportionalSymbolSeriesComponent.register();
        TypeRegistrar.registerCons("IgcGeographicProportionalSymbolSeriesComponent", IgcGeographicProportionalSymbolSeriesComponent);
        TypeRegistrar.register("GeographicProportionalSymbolSeries", (<any>GeographicProportionalSymbolSeries).$type);
    }
}
