import { IgcTileSeriesModule } from './igc-tile-series-module';

import { IgcGeographicTileSeriesComponent } from './igc-geographic-tile-series-component';
import { GeographicTileSeries } from './GeographicTileSeries';
import { TypeRegistrar } from 'igniteui-core/type';

export class IgcGeographicTileSeriesModule {
    public static register(): void {
IgcTileSeriesModule.register();
        IgcGeographicTileSeriesComponent.register();
        TypeRegistrar.registerCons("IgcGeographicTileSeriesComponent", IgcGeographicTileSeriesComponent);
        TypeRegistrar.register("GeographicTileSeries", (<any>GeographicTileSeries).$type);
    }
}
