import { IgcScatterAreaSeriesModule } from 'igniteui-charts/igc-scatter-area-series-module';

import { IgcGeographicScatterAreaSeriesComponent } from './igc-geographic-scatter-area-series-component';
import { GeographicScatterAreaSeries } from './GeographicScatterAreaSeries';
import { TypeRegistrar } from 'igniteui-core/type';

export class IgcGeographicScatterAreaSeriesModule {
    public static register(): void {
IgcScatterAreaSeriesModule.register();
        IgcGeographicScatterAreaSeriesComponent.register();
        TypeRegistrar.registerCons("IgcGeographicScatterAreaSeriesComponent", IgcGeographicScatterAreaSeriesComponent);
        TypeRegistrar.register("GeographicScatterAreaSeries", (<any>GeographicScatterAreaSeries).$type);
    }
}
